import React from 'react';

import classes from './Portofolios.module.css';
import Portofolio from './Portofolio/Portofolio';
import Button from '../UI/Button/Button';

const portofolios = (props) => {

    const projects = [
        { name :'project name', 
          imagePath : 'https://cdn.pixabay.com/photo/2016/11/27/21/42/stock-1863880_1280.jpg',
          data : {
              projectType : 'Webdevelopement',
              projectDescription: `Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vivamus pellentesque, 
              enim non congue vestibulum, felis sem tincidunt mi, sed fringilla ante ipsum sed lacus. Integer conv
              allis felis ac nulla ornare eleifend. 
              Maecenas elementum magna ac risus sodales laoreet. Cras finibus mollis felis vel ullamcorper.`,
              direction: 'left'
          } 
          
        },
        { name :'MPoject_Name', 
          imagePath : 'https://cdn.pixabay.com/photo/2016/11/27/21/42/stock-1863880_1280.jpg',
          data : {
              projectType : 'Webdevelopement',
              projectDescription: `Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vivamus pellentesque, 
              enim non congue vestibulum, felis sem tincidunt mi, sed fringilla ante ipsum sed lacus. Integer conv
              allis felis ac nulla ornare eleifend. 
              Maecenas elementum magna ac risus sodales laoreet. Cras finibus mollis felis vel ullamcorper.`,
              direction: 'right'
          } 
        }
    ];

    const portofolioItems = projects.map(portofolio => {
        return (<Portofolio key={portofolio['name']} imagePath={portofolio.imagePath} data={portofolio.data} projectName={portofolio.name}/>);
    });

    return(
        <div className={classes.Portofolios} id='portofolio'>
            <h1 className={classes.Portofolios_heading}>Portofolio:</h1>
            {portofolioItems}
            <Button toLink='/portofolio'>Full Portofolio</Button>
        </div>
    );
}

export default portofolios;