import React from 'react';

import classes from './AboutMe.module.css';
import peter from '../../assests/images/peter.jpg'

const aboutMe = (props) => {
    const educations = [
        {institution: "Lorem ipsum3", period: "2010-520"},
        {institution: "Lorem ipsum4", period: "2010-520"},
        {institution: "Lorem ipsum2", period: "2010-520"},
        {institution: "Lorem ipsum1", period: "2010-520"}
    ];

    const certificates = [
        {name: 'Nodejs',institution : "Lorem ipsum", year: "2010-520"},
        {name: 'CS50',institution : "Lorem ipsum", year: "2010-520"},
        {name: 'rEACT',institution : "Lorem ipsum", year: "2010-520"},
    ]
    return (
        <div className={classes.AboutMe}  id="about">
            <div className={classes.AboutMe_Column_1}>
                <div className={classes.AboutMe_Image}>
                    <img src={peter} alt="Myimage"/>
                </div>
                <h4 style={{textShadow:" -8px 8px 13px rgba(0, 0, 0, 0.30)"}}>Peter Bilolo Badibake</h4>
            </div>
            <div className={classes.AboutMe_Column_2}>
                <div className={classes.Bio}>
                    <h1 className={[classes.remove_space, classes.Main_heading].join(' ')}>About me:</h1>
                    <p className={[classes.remove_space].join(' ')}>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum</p>
                </div>
                <div className={classes.Achievement}>
                    <div className={classes.Education}>
                        <h1 className={[classes.remove_space, classes.Achievement_heading].join(' ')}>My education:</h1>
                        <ul className={classes.Achievement_list}>
                            { educations.map(education => {
                                return(
                                <li key={education.institution}>{education.institution}({education.period})</li>
                                );
                            })}
                        </ul>
                    </div>
                    <div className={classes.Certification}>
                        <h1 className={[classes.remove_space, , classes.Achievement_heading].join(' ')}>My certificates:</h1>
                        <ul  className={classes.Achievement_list}>
                            {certificates.map(certificate => {
                                return(
                                <li key={certificate.name}>{certificate.name}({certificate.institution})</li>
                                );
                            })}
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default aboutMe;