import React from 'react';

import {SocialIcon} from 'react-social-icons';
import classes from './SocialIcon.module.css';

const socialIcon = (props)  =>{
    return(
            // <SocialMediaIconsReact 
            // borderColor="rgba(255,255,255,255)" 
            //     borderWidth="2" borderStyle="solid" 
            //     icon={props.icon} iconColor="rgba(255,255,255,1)" 
            //     iconSize="4" 
            //     roundness="50%" 
            //     url={props.configuration.link} 
            //     size="50" 
            //     backgroundColor="rgba(255,255,255,1) transparent" 
                
            // />
            <SocialIcon 
            hey={props.icon}
            network={props.icon}
            bgColor="none"
            fgColor="#FFFFFF"
            url={props.configuration.link} 
            className={[classes.SocialIcon, classes[props.icon]].join(' ')}
            />
      
    );
}


export default socialIcon;